.icon-line {
  @include bg('icon_line.png');
  margin: 0 10px; }

.icon-phone2 {
  @include bg('icon_phone2.png');
  margin: 0 10px; }

.tempHide {
  transform: translate(0, 50px);
  opacity: 0;
  @include dTime(.8s);

  &.left {
    transform: translate(-50px, 0px); }
  &.right {
    transform: translate(50px, 0px); }
  &.top {
    transform: translate(0px, -50px); }
  &.show {
    opacity: 1;
    transform: translate(0, 0); } }

.blocker {
  z-index: 200;
  padding: 15px; }
.modal {
  // max-width: 1024px
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 250px;
  @include phone {
    padding: 50px 25px; }
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: rgba(black,.7); }

  .modal-container {
    max-width: 960px;
    position: relative;
    .clozBtn {
      @include bg('cloz.png');
      position: absolute;
      top: -60px;
      right: -9px; } } }
