// index.sass

#chatBtn {
  @include bg('floatBtn.png');
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 20;
  @include wobble-vertical;
  @include dTime;
  opacity: 0;
  cursor: pointer;
  &.show {
    opacity: 1; } }


#scrollBtn {
  @include bg('scrollBtn.png');
  position: fixed;
  left: 50%;
  bottom: 10px;
  margin-left: -52px;
  z-index: 10;
  @include updown;
  @include dTime;
  opacity: 0;
  &.show {
    opacity: 1; }
  @include phone {
    bottom: 100px; } }

#sideContainer {
  position: fixed;
  right: 15px;
  z-index: 20;
  top: 50%;
  transform: translateY(-50%);
  list-style-type: none;

  li {
    width: 8px;
    margin: 15px 0;
    height: 8px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 2px 2px 5px rgba(black,.3);
    &.active {
      background-color: #8dc556; } } }


.index {
  width: 100%;
  height: 100vh;
  @include coverBg;
  @include bgi('bg1_1.jpg');
  @include phone {
    @include bgi('bg1_1_m.jpg');
    height: 160vw; }
  .content {
    width: 65%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    @include phone {
      width: 100%;
      max-width: 100%;
      padding: 0 12%;
      padding-top: 150px;
      align-items: flex-start; }
    .p-title {
      text-align: center;
      width: 100%; } } }


.p2 {
  padding: 150px 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include coverBg('bg2.jpg');
  @include phone {
    @include bgi('bg2_m.jpg');
    padding: 100px 50px; }
  .content {
    text-align: center;
    line-height: 2.5em; } }

.p3 {
  height: 100vh;
  @include coverBg('bg3.jpg');
  @include phone {
    @include bgi('bg3_m.jpg');
    height: 100vw; }
  position: relative;
  .content {
    position: absolute;
    bottom: 25%;
    right: 10%;
    max-width: 380px;
    line-height: 2.5em;
    @include phone {
      right: auto;
      bottom: auto;
      top: 45%;
      transform: translate(0, -50%);
      left: 33vw;
      width: 65vw; } } }

.p4 {
  height: 100vh;
  @include coverBg('bg4.jpg');
  @include phone {
    @include bgi('bg4_m.jpg');
    height: 140vw; }
  position: relative;
  .content {
    position: absolute;
    top: 15%;
    left: 4%;
    line-height: 2.5em;
    @include phone {
      top: 8%;
      left: 4%;
      right: 20%; }
    h1 {
      font-size: 26px; }
    h2 {
      font-size: 28px; } }

  .stamp {
    position: absolute;
    bottom: 40px;
    right: 150px;
    padding: 20px 30px;
    border: 1px solid white;
    border-radius: 5px;
    @include phone {
      right: 20px; } } }

.p5 {
  height: 100vh;
  @include coverBg('bg5.jpg');
  @include phone {
    @include bgi('bg5_m.jpg');
    height: 180vw; }
  position: relative;
  .content {
    position: absolute;
    left: 50%;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    line-height: 2.5em;
    > div {
      max-width: 666px;
      width: 100%; }
    @include phone {
      left: 0;
      width: 100%;
      top: 0;
      height: 55%;
      padding: 0 20px;
      > div {
        max-width: 100%; } }


    h1 {
      font-size: 26px; }
    h2 {
      font-size: 28px; }
    .dot-line {
      @include bg('p5_t4.png');
      max-width: 100%; }
    .certificates {
      display: flex;
      @include phone {
        justify-content: space-between; }
      .certificate {
        cursor: pointer;
        margin-right: 25px;
        @include phone {
          margin-right: 0; } } } }
  .stamp {
    position: absolute;
    bottom: 20px;
    left: 20px;
    padding: 20px 30px;
    border: 1px solid rgba(black,.4);
    border-radius: 5px;
    @include phone {
      padding: 15px 20px;
      img {
        width: 150px; } } } }

.p6 {
  height: 100vh;
  position: relative;
  @include coverBg('bg6.jpg');
  background-attachment: fixed;
  @include phone {
    @include bgi('bg6_m.jpg');
    height: 125vw;
    background-attachment: inherit; }
  .content {
    padding: 50px 50px 30px;
    border: 1px solid rgba(white,.4);
    border-radius: 5px;
    position: absolute;
    bottom: 70px;
    right: 150px;
    max-width: 554px;
    @include phone {
      bottom: 25px;
      right: auto;
      width: 95%;
      left: 50%;
      transform: translate(-50%, 0); } } }

.store {
  background-color: white;
  padding: 100px 0;
  min-height: 600px;
  .content {
    text-align: center; } }
